<script>
/* eslint-disable */
import { Message } from 'element-ui';
import { M } from '../../../service/grpc.js';
import { rpc } from '../../../service/index.js';
import { getToken } from '../../../service/helper.js';

let token = '';

const CompWrap = {
  functional: true,
  render(h, context) {
    return (
      <div class="container">
        <div class="wrap">{context.children}</div>
      </div>
    );
  }
};

const CompList = {
  functional: true,
  props: ['label', 'text', 'gutter', 'nomb'],
  render(h, context) {
    const { label, text, gutter, nomb } = context.props;
    const arr = ['info'];

    if (gutter !== undefined) arr.push('g-b');
    if (nomb !== undefined && gutter === undefined) arr.push('nomb');

    return (
      <div class={arr.join(' ')}>
        <div class="color1 mr-l pr-l title">{label}</div>
        <div class="color1 ml-l pl-l text">{text}</div>
      </div>
    );
  }
};

const map = {
  '0': '等待买家支付',
  '1': '拼团中',
  '2': '待发货',
  '3': '等待买家收货',
  '4': '买家已收货',
  '5': '团购失败',
  '6': '买家已取消订单',
  '7': '退货中',
  '8': '已退款',
  '9': '买家申请退货',
  '10': '拒绝买家退货',
  '11': '同意退货但拒绝退款',
  '12': '买家申请退款'
};

export default {
  components: {
    'comp-list': CompList,
    'comp-wrap': CompWrap
  },
  data() {
    return {
      shiplist: [],
      shipindex: null,
      map,
      order: {
        address: {},
        invoice: {},
        marketinglist: [],
        orderproductsList: [],
        user: {},
        coupon: {}
      }
    };
  },
  async created() {
    token = getToken();
    rpc(
      'StoreDeliveryService.GetDeliveryList',
      M('DeliveryListQueryRequest', {
        token: M('Token', { token }),
        count: 100
      })
    ).then(res => {
      var l = [];
      res.deliverylistList.forEach(v => {
        if (v.deliveryid === 'BEST') {
          l.push({
            name: `${v.deliveryname}-标准快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '标准快递'
          });
        }
        if (v.deliveryid === 'DB') {
          l.push({
            name: `${v.deliveryname}-大件快递3.60(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-大件快递3.60`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '大件快递3.60'
          });
          l.push({
            name: `${v.deliveryname}-特准快件(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-特准快件`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 2,
            servicevalue: '特准快件'
          });
        }
        if (v.deliveryid === 'EMS') {
          l.push({
            name: `${v.deliveryname}-标准快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 6,
            servicevalue: '标准快递'
          });
          l.push({
            name: `${v.deliveryname}-快递包裹(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-快递包裹`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 9,
            servicevalue: '快递包裹'
          });
        }
        if (v.deliveryid === 'OTP') {
          l.push({
            name: `${v.deliveryname}-标准快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '标准快递'
          });
        }
        if (v.deliveryid === 'PJ') {
          l.push({
            name: `${v.deliveryname}-标准快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '标准快递'
          });
        }
        if (v.deliveryid === 'SF') {
          l.push({
            name: `${v.deliveryname}-标准快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 0,
            servicevalue: '标准快递'
          });
          l.push({
            name: `${v.deliveryname}-顺丰即日(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-顺丰即日`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '顺丰即日'
          });
          l.push({
            name: `${v.deliveryname}-顺丰次晨(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-顺丰次晨`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 2,
            servicevalue: '顺丰次晨'
          });
          l.push({
            name: `${v.deliveryname}-顺丰标快(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-顺丰标快`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 3,
            servicevalue: '顺丰标快'
          });
          l.push({
            name: `${v.deliveryname}-顺丰特惠(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-顺丰特惠`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 4,
            servicevalue: '顺丰特惠'
          });
        }
        if (v.deliveryid === 'STO') {
          l.push({
            name: `${v.deliveryname}-标准快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '标准快递'
          });
        }
        if (v.deliveryid === 'YTO') {
          l.push({
            name: `${v.deliveryname}-普通快递(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-普通快递`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 0,
            servicevalue: '普通快递'
          });
          l.push({
            name: `${v.deliveryname}-圆准达(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-圆准达`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 1,
            servicevalue: '圆准达'
          });
        }
        if (v.deliveryid === 'YUNDA') {
          l.push({
            name: `${v.deliveryname}-标准快件(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快件`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 0,
            servicevalue: '标准快件'
          });
        }
        if (v.deliveryid === 'ZTO') {
          l.push({
            name: `${v.deliveryname}-标准快件(${v.bizid})`,
            shippingcompany: `${v.deliveryname}-标准快件`,
            deliveryid: v.deliveryid,
            bizid: v.bizid,
            servicetype: 0,
            servicevalue: '标准快件'
          });
        }
      });
      l.push({
        name: '德邦快递-大件快递3.60(现付)',
        shippingcompany: '德邦快递-大件快递3.60',
        deliveryid: 'DB',
        bizid: 'DB_CASH',
        servicetype: 1,
        servicevalue: '大件快递3.60'
      });
      l.push({
        name: '德邦快递-特准快件(现付)',
        shippingcompany: '德邦快递-特准快件',
        deliveryid: 'DB',
        bizid: 'DB_CASH',
        servicetype: 2,
        servicevalue: '特准快件'
      });

      /*
      l.push({
        name: '承诺达特快-标准快递(现付)',
        shippingcompany: '承诺达特快-标准快递',
        deliveryid: 'OTP',
        bizid: 'OTP_CASH',
        servicetype: 1,
        servicevalue: '标准快递'
      });

      l.push({
        name: '品骏快递-标准快递(现付)',
        shippingcompany: '品骏快递-标准快递',
        deliveryid: 'PJ',
        bizid: 'PJ_CASH',
        servicetype: 1,
        servicevalue: '标准快递'
      });
        */

      l.push({
        name: '顺丰速运-标准快递(现付)',
        shippingcompany: '顺丰速运-标准快递',
        deliveryid: 'SF',
        bizid: 'SF_CASH',
        servicetype: 0,
        servicevalue: '标准快递'
      });
      l.push({
        name: '顺丰速运-顺丰即日(现付)',
        shippingcompany: '顺丰速运-顺丰即日',
        deliveryid: 'SF',
        bizid: 'SF_CASH',
        servicetype: 1,
        servicevalue: '顺丰即日'
      });
      l.push({
        name: '顺丰速运-顺丰次晨(现付)',
        shippingcompany: '顺丰速运-顺丰次晨',
        deliveryid: 'SF',
        bizid: 'SF_CASH',
        servicetype: 2,
        servicevalue: '顺丰次晨'
      });
      l.push({
        name: '顺丰速运-顺丰标快(现付)',
        shippingcompany: '顺丰速运-顺丰标快',
        deliveryid: 'SF',
        bizid: 'SF_CASH',
        servicetype: 3,
        servicevalue: '顺丰标快'
      });
      l.push({
        name: '顺丰速运-顺丰特惠(现付)',
        shippingcompany: '顺丰速运-顺丰特惠',
        deliveryid: 'SF',
        bizid: 'SF_CASH',
        servicetype: 4,
        servicevalue: '顺丰特惠'
      });
      l.push({
        name: '商家自配送',
        shippingcompany: '商家自配送',
        deliveryid: '',
        bizid: '',
        servicetype: 0,
        servicevalue: ''
      });
      this.shiplist = l;
    });

    const { id } = this.$route.query;

    // 获取订单
    const res = await rpc(
      'StoreOrderService.GetOrder',
      M('IDWithTokenRequest', {
        token: M('Token', { token }),
        id: id
      })
    );
    // 完善订单里的关联资源
    let productidlist = res.orderproductsList.map(v => v.productid);
    let reslist = await Promise.all([
      rpc(
        'StoreUserService.GetUser',
        M('IDWithTokenRequest', {
          token: M('Token', { token }),
          id: res.userid
        })
      ),
      rpc(
        'StoreProductService.GetProductList',
        M('ProductListQueryRequest', {
          token: M('Token', { token }),
          idlistList: productidlist,
          sortby: 1,
          sort: 1,
          count: productidlist.length
        })
      ),
      res.marketingidsList.length
        ? rpc(
            'StoreMarketingService.GetMarketingList',
            M('MarketingListQueryRequest', {
              token: M('Token', { token }),
              kind: -1,
              idlistList: res.marketingidsList,
              sortby: 1,
              sort: 1,
              count: res.marketingidsList.length
            })
          )
        : null,
      res.couponid
        ? rpc(
            'StoreCouponService.GetCoupon',
            M('IDWithTokenRequest', {
              token: M('Token', { token }),
              id: res.couponid
            })
          )
        : null,
      res.profitsharinguserid
        ? rpc(
            'StoreUserService.GetUser',
            M('IDWithTokenRequest', {
              token: M('Token', { token }),
              id: res.profitsharinguserid
            })
          )
        : null
    ]);
    res.user = reslist[0];
    res.orderproductsList.forEach(item => {
      item.product = reslist[1].productlistList.find(v => v.id === item.productid);
    });
    res.marketinglist = reslist[2] ? reslist[2].marketinglistList : [];
    res.coupon = reslist[3];
    res.profitsharinguser = reslist[4];
    productidlist = [];
    const coupontemplateidlist = [];
    res.marketinglist.forEach(item => {
      if (item.kind !== 2) {
        return;
      }
      if (item.gifttype === 0) {
        coupontemplateidlist.push(item.giftid);
      }
      if (item.gifttype === 1) {
        productidlist.push(item.giftid);
      }
    });
    reslist = await Promise.all([
      coupontemplateidlist.length
        ? rpc(
            'StoreCouponTemplateService.GetCouponTemplateList',
            M('CouponTemplateListQueryRequest', {
              token: M('Token', { token }),
              idlistList: coupontemplateidlist,
              count: coupontemplateidlist.length
            })
          )
        : null,
      productidlist.length
        ? rpc(
            'StoreProductService.GetProductList',
            M('ProductListQueryRequest', {
              token: M('Token', { token }),
              idlistList: productidlist,
              sortby: 1,
              sort: 1,
              count: productidlist.length
            })
          )
        : null
    ]);
    res.marketinglist.forEach(item => {
      item.coupontemplate = null;
      item.product = null;
      if (item.kind !== 2) {
        return;
      }
      if (item.gifttype === 0) {
        item.coupontemplate = reslist[0] ? reslist[0].coupontemplatelistList.find(v => v.id === item.giftid) : null;
      }
      if (item.gifttype === 1) {
        item.product = reslist[1] ? reslist[1].productlistList.find(v => v.id === item.giftid) : null;
      }
    });
    this.order = res;
  },

  methods: {
    updateOrder(status) {
      const row = JSON.parse(JSON.stringify(this.order));
      row.status = status;
      if (status === 3) {
        if (this.shipindex === null) {
          this.$message({
            message: '请选择物流公司',
            type: 'error'
          });
          return;
        }
        row.shippingcompany = this.shiplist[this.shipindex].shippingcompany;
      }
      rpc(
        'StoreOrderService.UpdateOrder',
        M('OrderWithTokenRequest', {
          token: M('Token', { token }),
          order: M('Order', {
            ...row,
            address: M('Address', row.address),
            invoice: M('Invoice', row.invoice),
            orderproductsList: row.orderproductsList.map(v => M('OrderProduct', v))
          }),
          ...this.shiplist[this.shipindex]
        })
      ).then(res => {
        this.order.status = res.status;
        this.order.shippingcompany = res.shippingcompany;
        this.order.shippingnumber = res.shippingnumber;
        this.order.updatedat = res.updatedat;
        this.$message({
          message: '操作成功',
          type: 'success'
        });
      });
    },
    getCoupon(coupon) {
      return `¥ -${this.$amount1(coupon.amount)} (满${this.$amount1(coupon.reach)}减${this.$amount1(coupon.amount)})`;
    },
    getMarketing(marketing) {
      if (marketing.kind === 1) {
        return `¥ -${this.$amount1(marketing.reduction)} (满${this.$amount1(marketing.reach)}减${this.$amount1(marketing.reduction)})`;
      }
      if (marketing.kind === 2) {
        if (marketing.gifttype === 1) {
          return `满${this.$amount1(marketing.reach)}赠商品: ${marketing.product.name}`;
        }
        let amountstr = '';
        if (marketing.coupontemplate.amount === 0) {
          amountstr = `¥ ${marketing.coupontemplate.amountbegin / 100} ~ ¥ ${marketing.coupontemplate.amountend / 100}`;
        }
        if (marketing.coupontemplate.amount === 1) {
          amountstr = `¥ ${marketing.coupontemplate.amount / 100}`;
        }
        return `满${this.$amount1(marketing.reach)}赠优惠券: ${marketing.coupontemplate.name}(${amountstr})`;
      }
      return `¥ -${this.$amount1(this.order.originfreight)} (满${this.$amount1(marketing.reach)}免运费)`;
    },
    getMarketingLabel(marketing) {
      const map = { 1: '满减优惠', 2: '满赠优惠', 3: '满免运费' };
      return map[marketing.kind];
    }
  }
};
</script>

